* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  background-image: url("ruvim-noga-o0R4iqk1eRQ-unsplash.ae198abc.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  display: flex;
  box-shadow: inset 0 0 0 100vmax #00000038;
}

section li {
  list-style: none;
}

.links {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
  font-size: 3rem;
  display: flex;
}

.links li {
  width: 100%;
  border-bottom: 3px solid #7560c0;
  list-style: none;
}

.links a {
  color: #000;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: flex;
}

.linkshover {
  color: #fcffe7;
  font-size: 4rem;
  transition: all .8s;
}

.headings, .navbar {
  display: none;
}

header {
  width: 100%;
  height: 60vh;
  color: #fcffe7;
  text-shadow: 0 15px 25px #c55b89;
  flex-direction: column;
  justify-content: center;
  font-size: 1.3rem;
  display: flex;
}

.social {
  justify-content: center;
  margin-left: 45px;
  padding-bottom: 50px;
  font-size: 2rem;
  display: flex;
}

.social li {
  margin: 30px;
}

.social a {
  color: #000;
}

section {
  box-shadow: 10px 15px 25px #75a5db;
}

section .infobar {
  height: 35vh;
  width: 100%;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 12rem;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

section .infobar li {
  list-style: none;
}

section .infobar .Me {
  color: #75a5db;
  margin-left: 35px;
  font-family: Monoton;
  font-size: 7rem;
  animation: rubberband 3s ease-out infinite;
}

@keyframes rubberband {
  0% {
    transform: scaleX(1px) scaleY(1);
  }

  40% {
    transform: scaleX(1.12px) scaleY(.75);
  }

  55% {
    transform: scaleX(.85)scaleY(1);
  }

  65% {
    transform: scaleX(1.09)scaleY(.85);
  }

  75% {
    transform: scaleX(.9)scaleY(1);
  }

  90% {
    transform: scaleX(1.05)scaleY(.95);
  }

  100% {
    transform: scaleX(1)scaleY(1);
  }
}

section .infobar .me {
  color: #fcffe7;
  margin-bottom: 5px;
  font-family: Monoton;
  font-size: 2.2rem;
}

section .infobar .me a {
  color: #75a5db;
  margin-left: 40px;
  text-decoration: none;
}

section .infobar .wd {
  color: #7560c0;
  margin-left: 15px;
  padding-bottom: 20px;
  font-family: Open Sans, sans-serif;
  font-size: 1.8rem;
}

li a:hover {
  color: #c55b89;
  cursor: cell;
}

@media screen and (min-width: 1025px) {
  button {
    display: none;
  }

  .headings {
    display: block;
  }

  span {
    display: none;
  }

  body {
    background-image: url("ruvim-noga-o0R4iqk1eRQ-unsplash.ae198abc.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }

  body header {
    width: 30%;
    color: #fcffe7;
    flex-direction: column;
    justify-content: center;
    margin-left: 50rem;
    padding-top: 280px;
    font-size: 1.8rem;
    display: flex;
  }

  body header p {
    color: #ff8c8c9d;
    margin-top: 30px;
    margin-left: 10px;
    font-family: Open Sans;
    font-weight: 400;
    text-decoration: overline;
  }

  body header h1 {
    font-size: 5rem;
  }

  body header b {
    font-size: 4.5rem;
  }

  body header h2 {
    font-size: 5rem;
  }

  body header .font {
    font-family: Monoton;
    font-size: 4.5rem;
  }

  body h1:hover, body h2:hover {
    color: #75a5db;
    text-shadow: 0 15px 25px #c55b89;
    font-size: 6rem;
    transition: all .8s;
  }

  body b:hover {
    color: #75a5db;
    text-shadow: 0 15px 25px #c55b89;
    font-size: 5.1rem;
    transition: all .8s;
  }

  @keyframes rubberband {
