      transform: scaleX(.85)scaleY(1);
    }

    65% {
      transform: scaleX(1.09)scaleY(.85);
    }

    75% {
      transform: scaleX(.9)scaleY(1);
    }

    90% {
      transform: scaleX(1.05)scaleY(.95);
    }

    100% {
      transform: scaleX(1)scaleY(1);
    }
  }

  body .sidebar {
    height: 100vh;
    width: 150px;
    background-color: #000;
    border-radius: 5px;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    position: fixed;
    top: 0;
    box-shadow: 10px 15px 25px #75a5db;
  }

  body .sidebar .infobar {
    display: block;
  }

  body .sidebar .infobar .Me {
    color: #75a5db;
    margin-left: 10px;
    font-family: Monoton;
    font-size: 7rem;
    animation: rubberband 3s ease-out infinite;
  }

  body .sidebar .infobar .me {
    color: #75a5db;
    margin-left: -20px;
  }

  body .sidebar .infobar .me a {
    margin-right: 50px;
    padding-right: 10px;
    font-family: Monoton;
    font-size: 2rem;
  }

  body .sidebar .infobar .wd {
    color: #7560c0;
    margin-left: 15px;
    font-family: Open Sans, sans-serif;
    font-size: 1.1rem;
  }

  body .sidebar li {
    list-style: none;
  }

  body .sidebar a {
    color: #75a5db;
    font-family: Open Sans, sans-serif;
    font-size: 1.5rem;
    text-decoration: none;
  }

  body .sidebar a:hover {
    font-size: 2rem;
    transition: all .8s;
  }

  body .sidebar .social {
    margin-left: 15px;
    display: flex;
  }

  body .sidebar .social li {
    margin: 7px;
  }

  body .sidebar .socialhover {
    font-size: 2.8rem;
    transition: all .8s;
  }

  body .links {
    padding-top: 370px;
    display: block;
  }

  body .links li {
    width: 100%;
    border-bottom: 3px solid #7560c0;
    margin-top: 20px;
    margin-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    list-style: none;
  }

  body .links a {
    padding-top: 15px;
  }
}

